<template>
  <v-container class="grey lighten-5">
    <!--// Create Quote //-->
    <create-quote-modal />

    <QuoteFilter />

    <v-row no-gutters class="mt-1">
      <v-col v-for="(quote, index) in quotes" :key="index" cols="12" sm="4">
        <QuoteCard :quote="quote" />
      </v-col>
    </v-row>
    <v-overlay :value="overlay">
      <v-progress-circular
        indeterminate
        size="64"
      ></v-progress-circular>
    </v-overlay>
  </v-container>
</template>

<script>
  import QuoteService from "../Services/QuoteService";
  import CreateQuoteModal from "../components/Quote/CreateQuoteModal.vue";
  import UpdateQuoteModal from "../components/Quote/UpdateQuoteModal.vue";
  import QuoteCard from "../components/Quote/QuoteCard.vue";
  import QuoteFilter from "../components/Quote/QuoteFilter.vue";

  export default {
    name: "Quotes",
    components: {
      CreateQuoteModal,
      UpdateQuoteModal,
      QuoteCard,
      QuoteFilter
    },
    data: () => ({
      quotes: [],
      overlay: true,
      error: ""
    }),
    mounted() {
      this.getQuotes();
    },
    methods: {
      async getQuotes() {
        try {
          this.quotes = await QuoteService.getQuotes();
          this.overlay = false;
        } catch (err) {
          this.error = err.message;
        }
      },
    }
  }
</script>

<style scoped>
  .filter {
    position: sticky;
    top: 65px;
    z-index: 999;
  }
</style>
