import axios from "axios";

const url = "api/quotes/";

class QuoteService {
  // Get quotes
  static getQuotes() {
    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.get(url);
        const data = res.data;
        resolve(
          data.map(
            quotes => ({
              ...quotes,
              createdAt: new Date(quotes.createdAt)
            })
          )
        );
      } catch (err) {
        reject(err);
      }
    });
  }
  // Get a quote
  // Create a quote
  static createQuote(data) {
    return axios.post(url, data);
  }
  // Update a quote
  static updateQuote(id, payload) {
    return axios.put(url + id, payload);
  }
  // Delete a quote
  static deleteQuote(id) {
    return axios.delete(url + id);
  }
}

export default QuoteService;
