<template>
  <v-card>
    <v-card-title>
      <span class="headline">Update Quote</span>
    </v-card-title>
    <v-card-text>
      <v-container>
        <v-form v-model="isValid">
        <v-row>
          <v-col cols="12" sm="6" class="pa-0 p-0 ma-0 m-0 pr-1">
            <v-col cols="12" class="pa-0 p-0 ma-0 m-0">
              <v-textarea
                counter
                label="Quote*"
                :rules="quoteRules"
                :value="quote.quoteText"
                v-model="quote.quoteText"
