                class="pa-0 p-0 ma-0 m-0"
              ></v-textarea>
            </v-col>
            <v-col cols="12" class="pa-0 p-0 ma-0 m-0">
              <v-text-field
                label="Author"
                :value="quote.quoteAuthor"
                v-model="quote.quoteAuthor"
                class="pa-0 p-0 ma-0 m-0"
              ></v-text-field>
