              <v-card-actions>
                <v-list-item class="grow">
                  <v-list-item-avatar color="grey darken-3">
                    <v-img
                      class="elevation-6"
                      alt=""
                      src="https://avataaars.io/?avatarStyle=Transparent&topType=ShortHairShortCurly&accessoriesType=Prescription02&hairColor=Black&facialHairType=Blank&clotheType=Hoodie&clotheColor=White&eyeType=Default&eyebrowType=DefaultNatural&mouthType=Default&skinColor=Light"
                    ></v-img>
                  </v-list-item-avatar>

                  <v-list-item-content>
                    <v-list-item-title
                      v-text="quote.quoteAuthor"
                    >
                    </v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </v-card-actions>
            </v-card>
          </v-col>
          <v-col cols="6" class="pa-0 p-0 ma-0 m-0 pr-3">
            <v-text-field
              label="Language*"
              aria-required="true"
              :rules="[v => !!v || 'Language is required']"
              :value="quote.lang"
              v-model="quote.lang"
              class="pa-0 p-0 ma-0 m-0"
            ></v-text-field>
          </v-col>
          <v-col cols="6" class="pa-0 p-0 ma-0 m-0">
            <v-text-field
              label="Category*"
