            ></v-text-field>
          </v-col>
          <v-col cols="12" class="pa-0 p-0 ma-0 m-0">
            <v-text-field
              label="Thumbnail (URL)"
              :value="quote.thumbnail_url"
              v-model="quote.thumbnail_url"
              class="pa-0 p-0 ma-0 m-0"
            ></v-text-field>
          </v-col>
          <v-col cols="12" class="pa-0 p-0 ma-0 m-0">
            <v-text-field
              label="Blob Image (Optional)"
              :value="quote.blob_image"
              v-model="quote.blob_image"
              class="pa-0 p-0 ma-0 m-0"
            ></v-text-field>
