            ></v-text-field>
          </v-col>
          <v-col
            cols="12"
            sm="6"
            class="pa-0 p-0 ma-0 m-0"
          >
            <v-select
              :items="['0-17', '18-29', '30-54', '54+']"
              label="Font Family*"
              :value="quote.font_family"
              v-model="quote.font_family"
              required
              class="pa-0 p-0 ma-0 m-0"
            ></v-select>
          </v-col>
          <v-col
            cols="12"
            sm="6"
            class="pa-0 p-0 ma-0 m-0"
          >
            <v-select
              :items="['12', '14', '16', '18', '20', '24', '36']"
              label="Font Size*"
              :value="quote.font_size"
              v-model="quote.font_size"
              required
              class="pa-0 p-0 ma-0 m-0"
            ></v-select>
          </v-col>
        </v-row>
        </v-form>
      </v-container>
      <small>*indicates required field</small>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn
        color="blue darken-1"
        text
        @click="closeModal"
      >
        Close
      </v-btn>
      <v-btn
        color="blue darken-1"
        text
        @click="editQuote(quote)"
        :disabled="!isValid"
      >
        Save
      </v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
    import QuoteService from "../../Services/QuoteService";

    export default {
      name: "UpdateQuoteModal",
      props: {
        quote: {},
      },
      data: () => ({
        showEditModal: false,
        isValid: true,
        error: '',
        quoteRules: [v => v.length <= 250 || "Max 250 characters", v => !!v || "Quote is required"]
      }),
      methods: {
        async editQuote(quote) {
          try {
            await QuoteService.updateQuote(quote._id, quote);
            this.closeModal();
            window.location = "/";
          } catch (err) {
            this.error = err.message;
          }
        },
        closeModal() {
          this.showEditModal = false;
          this.$emit('edit-modal', false);
        }
      }
    }
</script>

