<template>
  <v-row justify="end">
    <v-form v-model="isValid">
    <v-dialog v-model="dialog" persistent max-width="900px">

      <template v-slot:activator="{ on, attrs }">
        <v-btn color="purple" dark v-bind="attrs" v-on="on" outlined small class="mb-3" >
          <v-icon>mdi-plus</v-icon> Create Quote
        </v-btn>
      </template>

      <v-card>
        <v-card-title>
          <span class="headline">Create Quote</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12" sm="6" class="pa-0 p-0 ma-0 m-0 pr-1">
                <v-col cols="12" class="pa-0 p-0 ma-0 m-0">
                  <v-textarea
                    counter
                    label="Quote*"
                    :rules="quoteRules"
                    :value="newQuote.quoteText"
                    v-model="newQuote.quoteText"
                    rows="2"
                    aria-required="true"
                    no-resize
                    class="pa-0 p-0 ma-0 m-0"
                  ></v-textarea>
                </v-col>
                <v-col cols="12" class="pa-0 p-0 ma-0 m-0">
                  <v-text-field
                    label="Author"
                    :value="newQuote.quoteAuthor"
                    v-model="newQuote.quoteAuthor"
                    class="pa-0 p-0 ma-0 m-0"
                  ></v-text-field>
                </v-col>
              </v-col>
              <v-col cols="12" sm="6" class="pa-0 p-0 ma-0 m-0">
                <v-card
                  class="mx-auto pa-2 mr-3 mb-3"
                  tile
                  outlined
                  max-width="400"
                  max-height="200"
                >
                  <v-card-text
                    v-text="newQuote.quoteText"
                    class="title font-weight-regular"
                  >
                  </v-card-text>

                  <v-card-actions>
                    <v-list-item class="grow">
                      <v-list-item-avatar color="grey darken-3">
                        <v-img
                          class="elevation-6"
                          alt=""
                          src="https://avataaars.io/?avatarStyle=Transparent&topType=ShortHairShortCurly&accessoriesType=Prescription02&hairColor=Black&facialHairType=Blank&clotheType=Hoodie&clotheColor=White&eyeType=Default&eyebrowType=DefaultNatural&mouthType=Default&skinColor=Light"
                        ></v-img>
                      </v-list-item-avatar>

                      <v-list-item-content>
                        <v-list-item-title
                          v-text="newQuote.quoteAuthor"
                        >
                        </v-list-item-title>
                      </v-list-item-content>
                    </v-list-item>
                  </v-card-actions>
                </v-card>
              </v-col>
              <v-col cols="6" class="pa-0 p-0 ma-0 m-0 pr-3">
                <v-text-field
                  label="Language*"
                  required
                  :rules="[v => !!v || 'Language is required']"
                  :value="newQuote.lang"
                  v-model="newQuote.lang"
                  class="pa-0 p-0 ma-0 m-0"
                ></v-text-field>
              </v-col>
              <v-col cols="6" class="pa-0 p-0 ma-0 m-0">
                <v-text-field
                  label="Category*"
                  aria-required="true"
                  :rules="[v => !!v || 'Category is required']"
                  :value="newQuote.category"
                  v-model="newQuote.category"
                  class="pa-0 p-0 ma-0 m-0"
                ></v-text-field>
              </v-col>
              <v-col cols="12" class="pa-0 p-0 ma-0 m-0">
                <v-text-field
                  label="Thumbnail (URL)"
                  :value="newQuote.thumbnail_url"
                  v-model="newQuote.thumbnail_url"
                  class="pa-0 p-0 ma-0 m-0"
                ></v-text-field>
              </v-col>
              <v-col cols="12" class="pa-0 p-0 ma-0 m-0">
                <v-text-field
                  label="Blob Image (Optional)"
                  :value="newQuote.blob_image"
                  v-model="newQuote.blob_image"
                  class="pa-0 p-0 ma-0 m-0"
                ></v-text-field>
              </v-col>
              <v-col cols="6" class="pa-0 p-0 ma-0 m-0 pr-3">
                <v-text-field
                  label="Background Color (Optional)"
                  :value="newQuote.bg_color"
                  v-model="newQuote.bg_color"
                  class="pa-0 p-0 ma-0 m-0"
                ></v-text-field>
              </v-col>
              <v-col cols="6" class="pa-0 p-0 ma-0 m-0">
                <v-text-field
                  label="Text Color (Optional)"
                  :value="newQuote.color"
                  v-model="newQuote.color"
                  class="pa-0 p-0 ma-0 m-0"
                ></v-text-field>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                class="pa-0 p-0 ma-0 m-0 pr-3"
              >
                <v-select
                  :items="['0-17', '18-29', '30-54', '54+']"
                  label="Font Family*"
                  :value="newQuote.font_family"
                  v-model="newQuote.font_family"
                  required
                  class="pa-0 p-0 ma-0 m-0"
                ></v-select>
              </v-col>
              <v-col
                cols="12"
                sm="6"
                class="pa-0 p-0 ma-0 m-0"
              >
                <v-select
                  :items="['12', '14', '16', '18', '20', '24', '36']"
                  label="Font Size*"
                  :value="newQuote.font_size"
                  v-model="newQuote.font_size"
                  required
                  class="pa-0 p-0 ma-0 m-0"
                ></v-select>
              </v-col>
            </v-row>
          </v-container>
          <small>*indicates required field</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="blue darken-1"
            text
            @click="dialog = false"
          >
            Close
          </v-btn>
          <v-btn
            color="blue darken-1"
            text
            @click="createQuote"
            :disabled="!isValid"
          >
            Save
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    </v-form>
  </v-row>
</template>

<script>
    import QuoteService from "../../Services/QuoteService";

    export default {
      name: "CreateQuoteModal",
      props: {
        quote: {},
      },
      data: () => ({
        dialog: false,
        isValid: true,
        error: '',
        newQuote: {
          quoteText: '',
          quoteAuthor: '',
          thumbnail_url: '',
          blob_image: '',
          bg_color: '',
          color: '',
          font_family: '',
          font_size: '',
          lang: '',
          category: '',
        },
        quoteRules: [v => v.length <= 250 || "Max 250 characters", v => !!v || "Quote is required"]
      }),
      methods: {
        async createQuote() {
          try {
            if (this.newQuote.quoteText !== "") {
              await QuoteService.createQuote(this.newQuote);
              window.location = "/";
            }
          } catch (err) {
            this.error = err.message;
          }
        }
      }
    }
</script>

